	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nobody Listens Anyway","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2009-03-11-familysearch-conference/index.md absPath of file >>> MarkdownRemark","html":"<p>I'm at the FamilySearch conference today. Right now Phil Windely is talking about open data and the related technical formats - REST, Json, RDF, and Microformats. I think this is good information for this crowd since sharing data is key to genealogy research. For years the methods of sharing data and integrating systems has been complex requiring conversations and planning between the involved parties. If instead you open your data and provide it in simple formats that others can consume, interesting, serendipitous things start to happen.</p>","frontmatter":{"title":"FamilySearch conference","date":"March 11, 2009","image":null}}},"pathContext":{"path":"/familysearch-conference"}}

